import React from 'react';
import { Link as GatsbyLink } from 'gatsby';
import { Grid, Button, Theme, Card, CardContent, CardMedia, CardActionArea, Typography } from '@material-ui/core';
import { makeStyles } from '@material-ui/styles';
import { Link, useStaticQuery, graphql } from 'gatsby'
import { Seo, Hero, Lazy } from '../components';
import { FC } from '../util';

const useStyles = makeStyles((theme: Theme) => ({
    heroButtons: {
        marginTop: theme.spacing(4)
    }
}));

const IndexPage: FC = () => {
    const { allMarkdownRemark: { edges } } = useStaticQuery(graphql`
        {
            allMarkdownRemark(sort: { order: DESC, fields: [frontmatter___date] }) {
                edges {
                node {
                    excerpt(pruneLength: 250)
                    fields {
                    slug
                    }
                    frontmatter {
                    thumbnail {
                        childImageSharp {
                            fixed(width: 200, height: 200) {
                            ...GatsbyImageSharpFixed
                            }
                        }
                    }
                    title
                    date
                    category
                    draft
                    }
                }
                }
            }
        }
    `)

  const query = graphql`
        query {
            imageOne: file(relativePath: { eq: "gatsby-icon.png" }) {
            childImageSharp {
                fluid(maxWidth: 1000) {
                base64
                tracedSVG
                aspectRatio
                src
                srcSet
                srcWebp
                srcSetWebp
                sizes
                originalImg
                originalName
                }
            }
            }
        }`

    // data?.edges?
    const styles = useStyles();
    return (
        <>
            <Seo title="Home" />

                <Hero
                    title="Hi people"
                    description="Welcome to your new Gatsby site. Now go build something great with
          Typescript and Material-ui."
                >
                    <div className={styles.heroButtons}>
                        <Grid container spacing={4} direction="column" alignItems="stretch">
                                    {edges.map((item: any, index: number) => {
                                        const { node: { fields, frontmatter, excerpt } } = item
                                        return (
                                            <Grid item >
                                                <Button key={index} component={GatsbyLink}
                                                to={fields?.slug} >
                                                    <Card>
                                                        <CardActionArea>
                                                            <CardMedia
                                                            component="img"
                                                            height="140"
                                                            image={frontmatter?.thumbnail?.childImageSharp?.fixed?.src}
                                                            alt="green iguana"
                                                            />
                                                            <CardContent>
                                                            <Typography gutterBottom variant="h5" component="div">
                                                                {frontmatter?.title}
                                                            </Typography>
                                                            <Typography variant="body2" color="textSecondary">
                                                                {excerpt}
                                                                {/* Lizards are a widespread group of squamate reptiles, with over 6,000
                                                                species, ranging across all continents except Antarctica */}
                                                            </Typography>
                                                            </CardContent>
                                                        </CardActionArea>
                                                    </Card>
                                                </Button>
                                            </Grid>
                                        )
                                    })}
                        </Grid>
                    </div>
                </Hero>
        </>
    );
};

export default IndexPage;
